import React,{useState} from 'react';

const CommentSection = ({articleName,setArticleInfo})=>{
    const[userName,setUserName] = useState('');
    const[commentText,setCommentText] = useState('');
    const updateComment = async () => {
            const result = await fetch(`/api/articles/${articleName}/add-comment`,
            {
                method:'post',
                body:JSON.stringify({user:userName,comment:commentText}),
                headers:{
                    'Content-Type':"application/json",
                }
        
        })
        const data = await result.json();
        setArticleInfo(data);
        setCommentText('');
        setUserName('');
    }
return (
<div id="add-comment-form">
    <label>
        Name:<input type="text" value={userName} onChange={(event)=> setUserName(event.target.value)}></input>
    </label>
    <label>
        Comment:
        <textarea rows={4} cols={50} value={commentText}onChange={(event)=> setCommentText(event.target.value)}></textarea>
    </label>
<button onClick={()=>updateComment()}>Submit</button>
</div>
)}

export default CommentSection;