import React, { useEffect, useState} from 'react';
import articleContent from './article-content';
import ArticlesList from '../components/ArticlesList';
import NotFound from './NotFound';
import CommentsList from '../components/CommentsList';
import UpvoteSection from '../components/UpvoteSection';
import CommentSection from '../components/CommentSection';

const ArticlePage = ({ match }) => {
    const name = match.params.name;
    const article = articleContent.find(article => article.name === name);
    const [articleInfo,setArticleInfo] = useState({upvotes:0,comments:[]});
    useEffect(() => {
       const fetchData = async () =>{
          const result =  await fetch(`/api/articles/${name}`);
          console.log(result);
          const data = await result.json();
          setArticleInfo(data);
       }
       fetchData();
        },[name])
    
    if(!article) return <NotFound/>
    const otherArticles = articleContent.filter(article => article.name !== name);
    
    return(
        <>
       
        <h1>{article.title}</h1>
        <UpvoteSection articleName={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo}></UpvoteSection>
        {article.content.map((paragraph,key)=>(
            <p key={key}>{paragraph}</p>
        ))}
       
            <CommentsList commentInfo={articleInfo.comments}></CommentsList>
            <CommentSection articleName={name} setArticleInfo={setArticleInfo}></CommentSection>
        
        <h3>Other articles</h3>
        <ArticlesList articles={otherArticles}></ArticlesList>
        </>

    );

}
export default ArticlePage;