import React from 'react';

const CommentsList = ({ commentInfo }) => (
    <>
    <h3>Comments:</h3>
    {commentInfo.map((commentNew, key) => (
        <div className="comment" key={key}>
            <h4>{commentNew.user}</h4>
            <p>{commentNew.comment}</p>
        </div>
    ))}
    </>
);

export default CommentsList;